'use client';

import { useState } from 'react';
import { cn } from '@/lib/utils';

interface RatingInputProps {
  onSelect: (value: string) => void;
}

export function RatingInput({ onSelect }: RatingInputProps) {
  const [hoveredRating, setHoveredRating] = useState(0);

  const ratings = [1, 2, 3, 4, 5];

  return (
    <div className="flex flex-col items-center gap-6 sm:gap-8">
      <div className="text-center px-4">
        <p className="text-xl sm:text-2xl font-semibold text-foreground mb-2 flex items-center justify-center gap-2">
          <span className="text-2xl sm:text-3xl">üíï</span>
          How much do you love it?
        </p>
        <p className="text-xs sm:text-sm text-muted-foreground">
          Tap to rate from 1 to 5 hearts *
        </p>
      </div>
      <div className="flex flex-wrap justify-center gap-2 sm:gap-3 px-2">
        {ratings.map((rating) => (
          <button
            key={rating}
            onClick={() => onSelect(rating.toString())}
            onMouseEnter={() => setHoveredRating(rating)}
            onMouseLeave={() => setHoveredRating(0)}
            className="group relative transition-all hover:scale-110 active:scale-95 touch-manipulation"
          >
            <div
              className={cn(
                'flex items-center justify-center w-16 h-16 sm:w-20 sm:h-20 md:w-24 md:h-24 rounded-full transition-all',
                hoveredRating >= rating
                  ? 'scale-110 sm:scale-125 drop-shadow-2xl'
                  : 'scale-100 opacity-60 hover:scale-110'
              )}
            >
              <svg
                className="w-12 h-12 sm:w-14 sm:h-14 md:w-16 md:h-16 transition-all"
                fill={hoveredRating >= rating ? '#E63946' : '#ffc0c7'}
                viewBox="0 0 24 24"
              >
                <path d="M12,21.35L10.55,20.03C5.4,15.36 2,12.27 2,8.5C2,5.41 4.42,3 7.5,3C9.24,3 10.91,3.81 12,5.08C13.09,3.81 14.76,3 16.5,3C19.58,3 22,5.41 22,8.5C22,12.27 18.6,15.36 13.45,20.03L12,21.35Z" />
              </svg>
            </div>
            <span
              className={cn(
                'absolute -bottom-5 sm:-bottom-6 left-1/2 -translate-x-1/2 text-xs sm:text-sm font-medium',
                hoveredRating >= rating ? 'text-primary' : 'text-muted-foreground'
              )}
            >
              {rating}
            </span>
          </button>
        ))}
      </div>
      <p className="text-xs text-muted-foreground px-4 text-center">
        * Please select a rating to continue
      </p>
    </div>
  );
}
