'use client';

import { useEffect, useState } from 'react';
import { supabase } from '@/lib/supabase/client';
import { updateSessionTracking } from '@/actions/tracking';
import { Card, CardContent } from '@/components/ui/Card';
import { LikeDislikeInput } from './LikeDislikeInput';
import { RatingInput } from './RatingInput';
import { BlurredMedia } from './BlurredMedia';
import type { Question, DemographicsData } from '@/lib/types';

interface QuestionStepProps {
  question: Question;
  sessionId: string;
  surveyId: string;
  demographics: DemographicsData;
  onAnswer: (questionId: string, answerValue: string) => void;
  questionNumber: number;
  totalQuestions: number;
}

export function QuestionStep({
  question,
  sessionId,
  surveyId,
  demographics,
  onAnswer,
  questionNumber,
  totalQuestions,
}: QuestionStepProps) {
  const [startTime] = useState(Date.now());

  useEffect(() => {
    // Track question view
    updateSessionTracking({
      survey_id: surveyId,
      session_id: sessionId,
      current_page: questionNumber,
      question_id: question.id,
    });
  }, [question.id, sessionId, surveyId, questionNumber]);

  const handleAnswer = async (answerValue: string) => {
    // Save response to database
    const { error } = await supabase.from('responses').insert([
      {
        survey_id: surveyId,
        question_id: question.id,
        session_id: sessionId,
        age_range: demographics.age_range,
        gender: demographics.gender,
        answer_value: answerValue,
      },
    ]);

    if (error) {
      console.error('Error saving response:', error);
      alert('Failed to save your response. Please try again.');
      return;
    }

    // Move to next question
    onAnswer(question.id, answerValue);
  };

  return (
    <div className="space-y-6">
      <Card className="border-primary/20 shadow-lg">
        <CardContent className="pt-6">
          <div className="mb-6">
            <div className="flex flex-col sm:flex-row sm:items-start sm:justify-between gap-2 sm:gap-0 mb-2">
              <h2 className="text-xl sm:text-2xl font-bold text-foreground flex-1">
                {question.question_text}
              </h2>
              <span className="text-xs font-medium text-primary bg-primary-light px-2 py-1 rounded w-fit">
                Required *
              </span>
            </div>
            {question.description && (
              <p className="text-sm sm:text-base text-muted-foreground mt-2">
                {question.description}
              </p>
            )}
          </div>

          {question.media_url && (
            <div className="mb-6">
              <BlurredMedia
                src={question.media_url}
                alt="Question media"
                type={question.media_url.match(/\.(jpg|jpeg|png|gif|webp)$/i) ? 'image' : 'video'}
              />
            </div>
          )}

          <div className="mt-8">
            {question.question_type === 'like_dislike' ? (
              <LikeDislikeInput onSelect={handleAnswer} />
            ) : (
              <RatingInput onSelect={handleAnswer} />
            )}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
